import React, { Fragment, useState } from "react";
import ArrowDown from "../images/icon-arrow.jpg";
import Button from "./Button";

const Questions = (props) => {
  const [isOpen, setIsOpen] = useState();

  const openHandler = () => {
    setIsOpen((prevvalue) => {
      return !prevvalue;
    });
  };

  //   gap-x-64 sm:gap-x-96
  //   style={{ maxWidth: "700px" }}

  return (
    <Fragment>
      <div
        className="flex justify-between items-center py-5 border-b-2 cursor-pointer w-full"
        onClick={openHandler}
      >
        <h1 className="font-semibold text-gray-800">{props.question}</h1>
        <img className="w-5 h-3" src={ArrowDown} alt="arrowDown" />
      </div>
      {isOpen && (
        <p className="text-gray-500 leading-6 text-left py-2">{props.answer}</p>
      )}
    </Fragment>
  );
};

