import React from "react";
import Image1 from "../images/ipad.png";
import Button from "./Button";

const FirstContent = () => {
  return (
    <div className="flex flex-col md:flex-row justify-center items-center my-28 mx-10 sm:gap-x-24">
      <div style={{ maxWidth: "600px" }}>
        <img className="rounded-2xl shadow-2xl" src={Image1} alt="image1" />
        <div className="bg-light-blue w-full h-80 overflow-hidden -mt-64 -ml-14 rounded-3xl"></div>
      </div>
      <div
        className="flex flex-col justify-start items-center text-center sm:text-left mt-5 "
        style={{ maxWidth: "400px" }}
      >
        <h1 className="font-bold text-gray-700 text-4xl leading-normal">
          A Simple Bookmark Manager
        </h1>
        <p className="mt-4 text-gray-400 leading-6">
          Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do
          eiusmod tempor incididunt ut labore et dolore magna aliqua.sed do
          eiusmod tempor incididunt ut labore et dolore magna aliqua.
        </p>
        <div className="mt-10">
          <Button
            bgColor="bg-light-blue"
            textColor="text-white"
            text="Get it on Chrome"
            extraCss="p-3 font-semibold mx-3 my-2 hover:bg-blue-600"
          />
          <Button
            bgColor="bg-gray-100"
            textColor="text-gray-500"
            text="Get it on Firefox"
            extraCss="p-3 font-bold mx-3 my-2 hover:bg-gray-500 hover:text-gray-100"
          />
        </div>
      </div>
    </div>
  );
};

export default FirstContent;
