import React from "react";
import BrowserCard from "./BrowserCard";
import Image1 from "../images/logo-chrome.jpg";
import Image2 from "../images/logo-firefox.jpg";
import Image3 from "../images/logo-opera.jpg";

const ExtentionContent = () => {
  return (
    <div className="flex flex-col justify-center items-center text-center sm:mt-3 mx-10">
      <h1 className="text-gray-800 font-bold text-3xl tracking-wide">
        Download the extention
      </h1>
      <p className="text-gray-400 my-7 leading-6" style={{ maxWidth: "500px" }}>
        Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod
        tempor incididunt ut labore et dolore magna aliqua.sed do eiusmod tempor
        incididunt ut.
      </p>
      <div className="flex flex-col lg:flex-row lg:gap-x-10 justify-center items-center gap-y-10 mt-10 lg:-mt-10">
        <BrowserCard
          text="Add to Chrome"
          img={Image1}
          version="Minimum version 62"
        />
        <BrowserCard
          text="Add to Firefox"
          img={Image2}
          version="Minimum version 55"
          extraCss="lg:mt-20"
        />
        <BrowserCard
          text="Add to Opera"
          img={Image3}
          version="Minimum version 46"
          extraCss="lg:mt-40"
        />
      </div>
    </div>
  );
};

export default ExtentionContent;
