import React, { useState } from "react";
import Button from "./Button";
import ErrorImg from "../images/error-icon.png";

const Footer = () => {
  const [enteredEmail, setEnteredEmail] = useState("");
  const [isError, setIsError] = useState(false);

  const changeHandler = (e) => {
    const email = e.target.value;
    setEnteredEmail(email);
  };

  const submitHandler = (e) => {
    e.preventDefault();
    if (!enteredEmail.includes("@")) {
      setIsError(true);
    }
    if (enteredEmail.includes("@")) {
      setIsError(false);
    }
    setEnteredEmail("");
  };

  const error = (
    <div className="flex text-left absolute -mt-7">
      <img className="w-5 h-5" src={ErrorImg} alt="error" />
      <p className="text-sm text-red-300 mx-2">Please entered a valid email</p>
    </div>
  );

  return (
    <div className="w-full bg-light-blue relative">
      <div className="flex flex-col justify-center items-center mx-10 py-14">
        <p className="text-white text-sm tracking-widest font-mono uppercase">
          35,000+ already joined
        </p>
        <h1
          className="text-white font-bold text-3xl tracking-wide text-center mt-6"
          style={{ maxWidth: "500px" }}
        >
          Stay up-to-date with what we're doing
        </h1>
        <form
          className="flex flex-col sm:flex-row w-full mt-11 gap-x-5"
          style={{ maxWidth: "550px" }}
          action="/"
          onSubmit={submitHandler}
        >
          {isError && error}
          <input
            className="p-3 w-full h-12 rounded-lg placeholder-opacity-50 placeholder-gray-500 shadow-lg"
            type="text"
            placeholder="Enter your email address"
            onChange={changeHandler}
            value={enteredEmail}
          />

          <Button
            bgColor="bg-red-500"
            textColor="text-white"
            text="Contact Us"
            extraCss="p-3 font-semibold my-6 sm:my-0 sm:w-2/6 w-full"
            type="submit"
          />
        </form>
      </div>
    </div>
  );
};

export default Footer;
