import React from "react";

const Dropdown = (props) => {
  return (
    <div
      className="grid grid-rows-4 text-center items-center mt-10 divide-y px-10"
      onClick={props.toggle}
    >
      <span className="p-4 cursor-pointer" to="/">
        <h1 className="font-semibold text-gray-500 hover:text-light-blue">
          HOME
        </h1>
      </span>
      <span className="p-4 cursor-pointer" to="/menu">
        <h1 className="font-semibold text-gray-500 hover:text-light-blue">
          FEATURES
        </h1>
      </span>
      <span className="p-4 cursor-pointer" to="/about">
        <h1 className="font-semibold text-gray-500 hover:text-light-blue">
          PRICING
        </h1>
      </span>
      <span className="p-4 cursor-pointer" to="/contact">
        <h1 className="font-semibold text-gray-500 hover:text-light-blue">
          CONTACT
        </h1>
      </span>
    </div>
  );
};

export default Dropdown;
