import React from "react";
import Button from "./Button";

const Navbar = (props) => {
  return (
    <div className="flex flex-row justify-between items-center mt-10 mx-10 sm:mx-20 md:mx-16 lg:mx-40">
      <div className="flex justify-center items-center cursor-pointer">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          className="h-9 w-9 bg-light-blue text-white rounded-full p-2"
          viewBox="0 0 20 20"
          fill="currentColor"
        >
          <path d="M5 4a2 2 0 012-2h6a2 2 0 012 2v14l-5-2.5L5 18V4z" />
        </svg>
        <h1 className="ml-3 font-bold text-2xl text-gray-900">BOOKMARK</h1>
      </div>
      <div className="md:hidden cursor-pointer" onClick={props.toggle}>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          className="h-7 w-7"
          fill="none"
          viewBox="0 0 24 24"
          stroke="currentColor"
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            strokeWidth={2}
            d="M4 6h16M4 12h16M4 18h16"
          />
        </svg>
      </div>
      <div className="hidden md:block font-semibold tracking-wider">
        <span
          href="/"
          className="px-5 uppercase text-gray-500 hover:text-light-blue"
        >
          Features
        </span>
        <span
          href="/"
          className="px-5 uppercase text-gray-500 hover:text-light-blue"
        >
          Pricing
        </span>
        <span
          href="/"
          className="px-5 uppercase text-gray-500 hover:text-light-blue"
        >
          Contact
        </span>
        <Button
          bgColor="bg-red-400"
          textColor="text-white"
          text="LOGIN"
          extraCss="px-8 py-2 mx-4 font-semibold hover:bg-red-500"
        />
      </div>
    </div>
  );
};

export default Navbar;
