import React from "react";
import DotLine from "../images/bg-dots.jpg";
import Button from "./Button";

const BrowserCard = (props) => {
  return (
    <div
      className={`${props.extraCss} flex flex-col justify-center items-center shadow-lg rounded-2xl pt-16 pb-7`}
    >
      <img className="w-28 h-28" src={props.img} alt={props.text} />
      <h1 className="text-gray-800 font-bold text-2xl tracking-wide mt-9">
        {props.text}
      </h1>
      <p className="text-gray-400 text-lg pt-2 pb-5">{props.version}</p>
      <img
        className="py-4"
        style={{ maxWidth: "330px" }}
        src={DotLine}
        alt="dotline"
      />
      <Button
        bgColor="bg-light-blue"
        textColor="text-white"
        text="Add & Install Extension"
        extraCss="py-3 px-8 font-semibold mx-3 my-2 hover:bg-blue-600"
      />
    </div>
  );
};

export default BrowserCard;

